.backdrop.open {
    display: block;
}

.modal.open::-webkit-scrollbar {
    width: 25px;
    border-radius: 0 30px 30px 0;
}

.modal.open::-webkit-scrollbar-thumb {
    background-color: var(--button-background-color);
    border-radius: 0 30px 30px 0;
}

.modal.open::-webkit-scrollbar-track {
    background-color: transparent;
}

.modal.open {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 64px;
    box-sizing: border-box;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 566px;
    height: auto;
    min-height: 509px;
    border-radius: 30px;
    background: #fbfbfb;
    overflow-x: hidden;
    overflow-y: auto;
}

.closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 18px;
}

.formTitle {
    font-weight: 500;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--primary-text-color);
    padding-bottom: 20px;
}

.formWelcome {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 1.25;
    color: rgba(25, 26, 21, 0.5);
    padding-bottom: 40px;
    padding-right: 20px;
    max-width: 438px;
}

.formInput {
    padding-left: 18px;
    border: 1px solid rgba(25, 26, 21, 0.1);
    border-radius: 12px;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    justify-content: flex-start;
    flex-direction: column;
    column-gap: 9px;
    width: 438px;
    height: 52px;
    color: var(--button-background-color-active);
}

.formInput::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: var(--primary-text-color);
}

.errorMessage {
    font-size: 12px;
    color: rgba(25, 26, 21, 0.5);
}

.submitButton {
    border-radius: 30px;
    border: none;
    width: 438px;
    height: 52px;
    margin-top: 22px;
    background: var(--button-background-color);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fbfbfb;
    font-weight: 500;
    font-size: 16px;
    cursor: pointer;
}

.submitButton:hover,
.submitButton:focus {
    background: var(--button-background-color-active);
}